// src/js/main.js
import 'bootstrap';
import Swiper from 'swiper';
import { Navigation, Pagination, Autoplay, A11y, EffectFade } from 'swiper/modules';

// PhotoSwipe — СТАТИЧЕСКИЙ импорт (без dynamic import)
import PhotoSwipe from 'photoswipe';
import PhotoSwipeLightbox from 'photoswipe/lightbox';
import 'photoswipe/style.css';

// Hero (как было)
const heroSwiper = new Swiper('.js-hero-swiper', {
    modules: [Navigation, Pagination, Autoplay, A11y, EffectFade],
    slidesPerView: 1,
    loop: true,
    effect: 'fade',
    speed: 600,
    autoplay: { delay: 4000, disableOnInteraction: false },
    navigation: { nextEl: '.js-hero-next', prevEl: '.js-hero-prev' },
    pagination: { el: '.js-hero-pagination', clickable: true },
    a11y: { enabled: true }
});

// document.addEventListener('DOMContentLoaded', () => {
    // Слайдер в карточке
    const gallerySwiper = new Swiper('.js-gallery', {
        modules: [Navigation, Pagination, Autoplay, A11y, EffectFade],
        slidesPerView: 1,
        loop: true,
        effect: 'fade',
        speed: 600,
        autoplay: { delay: 4000, disableOnInteraction: false },
        navigation: { nextEl: '.js-gallery-next', prevEl: '.js-gallery-prev' },
        pagination: { el: '.js-gallery-pagination', clickable: true },
        a11y: { enabled: true }
    });

    // // Лайтбокс (без dynamic import)
    const lightbox = new PhotoSwipeLightbox({
        gallery: '.js-gallery',
        children: '.gallery-item',
        pswpModule: PhotoSwipe,
        preload: [1, 1],
        bgOpacity: 0.9
    });
    lightbox.init();

    // Синхронизация активного слайда (опционально)
    lightbox.on('contentActivate', (e) => {
        const a = e?.content?.element;
        const slideEl = a?.closest('.swiper-slide');
        if (!slideEl) return;
        const index = [...slideEl.parentElement.children].indexOf(slideEl);
        if (index >= 0) gallerySwiper.slideToLoop(index, 0, false);
    });
// });

